@import url("https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,200;0,300;0,400;0,500;0,700;1,200;1,400;1,600;1,700&display=swap");

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #0b2434;
  padding: 20px;
}

main {
  background-color: #f5f5f5;
  height: 600px;
  max-width: 800px;
  border-radius: 15px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

header {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  margin-left: auto;
  width: 60%;
}
.title {
  font-size: 3rem;
  margin: 0;
  text-align: center;
}

.instructions {
  font-size: 1.5rem;
  margin: 0;
}
.counter {
  font-size: 1.5rem;
  background: rgba(80, 53, 255, 1);
  border-radius: 10px;
  color: rgba(255, 255, 255, 1);
  width: 160px;
  height: 35px;
  text-align: center;
  box-shadow: 0px 3.2px 7.7px 0px rgba(0, 0, 0, 0.18);
}

.dice-container {
  display: grid;
  grid-template: auto auto / repeat(5, 1fr);
  gap: 40px;
}

.die-face {
  height: 100px;
  width: 100px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  cursor: pointer;
}

.die-value {
  font-size: 3rem;
}

.button {
  border: none;
  background: rgba(80, 53, 255, 1);
  box-shadow: 0px 3.2px 7.7px 0px rgba(0, 0, 0, 0.18);
  border-radius: 10px;
  color: rgba(255, 255, 255, 1);
  font-family: "Karla", sans-serif;
  font-weight: bolder;
  height: 100px;
  width: 250px;
  font-size: 2.5rem;
  cursor: pointer;
}

.button:focus {
  outline: none;
}

.button:active {
  box-shadow: inset -1px 3px 8px 5px rgba(0, 0, 0, 0.7);
}

@media (max-width: 700px) {
  main {
    height: 95vh;
    max-width: 800px;
    border-radius: 15px;
    padding: 20px;
  }

  header {
    width: 100%;
    display: flex;
    flex-flow: column;
    align-items: center;
  }

  .title {
    font-size: 2.5rem;
  }
  .counter {
    font-size: 1.4rem;
    border-radius: 10px;
    width: 6.4em;
    text-align: center;
    margin: 0.75rem 0 0.75rem;
  }

  .instructions {
    font-size: 1.25rem;
    line-height: 1.2;
    text-align: center;
  }

  .dice-container {
    grid-template: repeat(5, 1fr) / auto auto;
    gap: 1.5em;
    margin-top: 0.75rem;
  }

  .die-face {
    height: 4em;
    width: 4em;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 5px;
  }

  .die-value {
    font-size: 2.5rem;
  }

  .button {
    margin-top: 0.75rem;
    font-size: 1.5rem;
    height: 2em;
    width: 6.4em;
  }
}
